import React from 'react';
import PropTypes from 'prop-types';
import { Select } from '@material-ui/core';
import Typography from '@material-ui/core/Typography';
import InputLabel from '@material-ui/core/InputLabel';
import FormControl from '@material-ui/core/FormControl';
import Button from '@material-ui/core/Button';
import TextField from '@material-ui/core/TextField';
import Box from '@material-ui/core/Box';
import Snackbar from '@material-ui/core/Snackbar';
import { makeStyles } from '@material-ui/core/styles';
import { bleDevice } from './ble'

const useStyles = makeStyles((theme) => ({
  box: {
    marginTop: theme.spacing(1.0),
    marginBottom: theme.spacing(0.5),
  },
  formControl: {
    marginTop: theme.spacing(1),
    minWidth: 120,
  },
  title: {
    fontSize: '150%',
    fontWeight: 'bold',
  },
  button: {
    marginTop: theme.spacing(2),    
    fontSize : '150%',
  }
}));

const isWordId            = 0;
const isCfThreshold       = 1;
const isVdThreshold       = 2;
const isGain              = 3;
const isNoiseSuppressor   = 4;
const isBeamForming       = 5;

function PageIsVoiceRecogSettings (props) {
  const classes = useStyles();
  const paramsValue = React.useRef([
    '', '', '', '', '', '',
  ]);
  const [buttonClicked, setButtonClicked] = React.useState(false);
  const [snackbarState, setSnackbarState] = React.useState({
    open : false,
    vertical : 'bottom',
    horizontal: 'center',
    message : '',
    autoHideDuration : 1000,
  });

  const handleSnackbarOpen = (text) => {
    setSnackbarState({...snackbarState, open : true, message : text});
  }

  const handleSnackbarClose = () => {
    setSnackbarState({...snackbarState, open: false});
  }

  const stringToNumber = (array) => {
    for (let i = 0; i < array.length; i++) {
      array[i] = array[i] - 0; // string -> number
    }
  }

  const handleSendButtonClicked = () => {
    const sendParamsValue = [...paramsValue.current];

    if (props.wordInfo.length <= 1) {
      console.log('No wordInfo');
    }
    else {
      setButtonClicked(true);

      /* String -> Number */
      stringToNumber(sendParamsValue);

      /* Send data */
      return Promise.resolve()
      .then(_=> {
        return bleDevice.writeValueWithResponse (bleDevice.bleInfo.device,
                                                 bleDevice.bleInfo.characteristicInfo.voiceRecogParams.characteristic,
                                                 [sendParamsValue[isWordId], 
                                                  sendParamsValue[isCfThreshold],
                                                  (sendParamsValue[isVdThreshold] >> 8),
                                                  (sendParamsValue[isVdThreshold] & 0xFF),
                                                  sendParamsValue[isGain],
                                                  sendParamsValue[isNoiseSuppressor],
                                                  sendParamsValue[isBeamForming]])
      })
      .then(_ => {
        if (bleDevice.bleInfo.device.gatt.connected) {
          handleSnackbarOpen('Success');

          /* Set Confindence Threshold */
          if (sendParamsValue[isWordId] === 0) {
            for (let wordId = 1; wordId < props.wordInfo.length; wordId++) {
              props.wordInfo[wordId].parameters[isCfThreshold] = sendParamsValue[isCfThreshold];
            }
          }
          else {
            props.wordInfo[sendParamsValue[isWordId]].parameters[isCfThreshold] = sendParamsValue[isCfThreshold];
          }

          /* Set other parameters */
          for (let paramId = isVdThreshold; paramId < props.wordInfo[1].parameters.length; paramId++) {
            props.wordInfo[1].parameters[paramId] = sendParamsValue[paramId];
          } 
        }
        else {
          // nothing
        }
        setButtonClicked(false);
        console.log('success');
      })
      .catch(error => {
        handleSnackbarOpen('Failed');
        setButtonClicked(false);
        console.log(error);
      });
    }
  }

  return (
    <div>
      <SelectConfideceThreshold wordInfo = {props.wordInfo} paramsValue = {paramsValue.current} />

      <SelectVoiceDetectionThreshold wordInfo = {props.wordInfo} paramsValue = {paramsValue.current} />

      <SelectGain wordInfo = {props.wordInfo} paramsValue = {paramsValue.current} />

      <SelectNoiseSuppressor wordInfo = {props.wordInfo} paramsValue = {paramsValue.current} />

      <SelectBeamForming wordInfo = {props.wordInfo} paramsValue = {paramsValue.current} />

      <Button 
      className = {classes.button} 
      variant = 'outlined' 
      color = 'primary' 
      fontWeight = 'bold'
      disabled = {Boolean(buttonClicked)}
      onClick = {handleSendButtonClicked}
      >
        APPLY
      </Button>

      <Snackbar
        open={snackbarState.open}
        onClose={handleSnackbarClose}
        message={snackbarState.message}
        key={snackbarState.vertical + snackbarState.horizontal}
        autoHideDuration={snackbarState.autoHideDuration}
      />

    </div>
  );
}

function InputParameterValue (props) {
  const classes = useStyles();
  const [paramvalue, setParamValue] = React.useState(props.paramsValue[props.parameterId]);

  const handleParamValueChanged = (event) => {
    let value = event.target.value;

    if (value > props.maxValue) {
      value = props.maxValue;
    }
    else if (value < props.minValue) {
      value = props.minValue;
    }
    else {
      // nothing
    }

    props.paramsValue[props.parameterId] = value;
    setParamValue(props.paramsValue[props.parameterId]);
  }

  React.useEffect(() => {
    if (props.wordInfo.length <= 1) {
      console.log('No wordInfo');
    }
    else if (props.wordId === '0') {
      props.paramsValue[props.parameterId] = '';
      setParamValue(props.paramsValue[props.parameterId]);
    }
    else {
      props.paramsValue[props.parameterId] = props.wordInfo[props.wordId].parameters[props.parameterId] + ''; // number -> string
      setParamValue(props.paramsValue[props.parameterId]);
    }
    
  }, [props.wordInfo, props.wordId]);

  return (
    <div>
      <Box className = {classes.box} mt = {1} >
        <TextField
          id = 'param-value-text-field'
          label='Value'
          type='number'
          value = {paramvalue}
          onChange = {handleParamValueChanged}
        />
      </Box>
    </div>
  );
}

function SelectConfideceThreshold (props) {
  const classes = useStyles();
  const [wordId, setWordId] = React.useState('1');

  const handleWordIdChanged = (event) => {
    const value = event.target.value;

    if (value === '-1') {
      // nothing
    }
    else {
      props.paramsValue[isWordId] = value;
      setWordId(props.paramsValue[isWordId]);
    }    
  }

  React.useEffect(() => {
    if (props.wordInfo.length <= 1) {
      console.log('No wordInfo');
    }
    else{
      props.paramsValue[isWordId] = wordId;
      setWordId(props.paramsValue[isWordId]);
    }
    
  }, [props.wordInfo]);

  return (
    <div>
      <Box className = {classes.box}>
        <Typography className = {classes.title}>
          Confidence Threshold
        </Typography>

        <Box display = 'flex' flexDirection = 'row'>
          <Box display = 'flex' flexDirection = 'column'>
          {
            ((props.wordInfo.length <= 1)
            ?
              (
                <FormControl className={classes.formControl}>
                <InputLabel >Word ID</InputLabel>
                <Select
                  native
                  value={wordId}
                  onChange={handleWordIdChanged}
                >
                  <option aria-label='None' value={'-1'} />
                </Select>
                </FormControl>
              )
            :
              (
                <FormControl className={classes.formControl}>
                <InputLabel >Word ID</InputLabel>
                <Select
                  native
                  value={wordId}
                  onChange={handleWordIdChanged}
                >
                  {
                    props.wordInfo.map((info, index) => (
                      ((index === 0) ? 
                        (
                          <option value={'0'} key={index}>All</option>
                        )
                      :
                        (
                        <option value={info.wordId} key={index}>
                          No. {info.wordId}
                        </option>
                        )
                      )
                    ))
                  }
                </Select>
                </FormControl>
              )
            )
          }
          </Box>

          <Box display = 'flex' flexDirection = 'column' ml = {1.0}> 
            <InputParameterValue
            wordInfo = {props.wordInfo}
            wordId = {wordId}
            paramsValue = {props.paramsValue}
            parameterId = {isCfThreshold}
            maxValue = {200}
            minValue = {0}
            />
          </Box>
        </Box>
      </Box>
    </div>

  );
}

function SelectVoiceDetectionThreshold (props) {
  const classes = useStyles();
  return (
    <div>
      <Box className = {classes.box} mt = {1}>
        <Typography className = {classes.title}>
          Voice Detection Threshold
        </Typography>

        <InputParameterValue
        wordInfo = {props.wordInfo}
        wordId = {1}
        paramsValue = {props.paramsValue}
        parameterId = {isVdThreshold}
        maxValue = {15000}
        minValue = {0}
        />
      </Box>
    </div>
  );
}

function SelectGain (props) {
  const classes = useStyles();  
  return (
    <div>
      <Box className = {classes.box} mt = {1} >
        <Typography className = {classes.title}>
          Gain
        </Typography>

        <InputParameterValue
        wordInfo = {props.wordInfo}
        wordId = {1}
        paramsValue = {props.paramsValue}
        parameterId = {isGain}
        maxValue = {9}
        minValue = {0}
        />
      </Box>
    </div>
  );
}

function SelectNoiseSuppressor (props) {
  const classes = useStyles();
  return (
    <div>
      <Box className = {classes.box} mt = {1} >
        <Typography className = {classes.title}>
          Noise Suppressor
        </Typography>

        <InputParameterValue
        wordInfo = {props.wordInfo}
        wordId = {1}
        paramsValue = {props.paramsValue}
        parameterId = {isNoiseSuppressor}
        maxValue = {7}
        minValue = {0}
        />
      </Box>
    </div>
  );
}

function SelectBeamForming (props) {
  const classes = useStyles();  
  return (
    <div>
      <Box className = {classes.box} mt = {1} >
        <Typography className = {classes.title}>
          Beam Forming
        </Typography>

        <InputParameterValue
        wordInfo = {props.wordInfo}
        wordId = {1}
        paramsValue = {props.paramsValue}
        parameterId = {isBeamForming}
        maxValue = {7}
        minValue = {0}
        />
      </Box>
    </div>
  );
}


PageIsVoiceRecogSettings.propTypes = {
    window: PropTypes.func,
  };

export { PageIsVoiceRecogSettings };